@import url(https://fonts.googleapis.com/css2?family=Wix+Madefor+Text:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

/* MainApp.css */
.output-box {
  position: relative;
  padding: 16px;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  background-color: #f9f9f9;
}

.copy-button {
  position: absolute;
  bottom: 8px;
  right: 8px;
}

.loading-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100px;
  width: 100%;
}

/* WixMadeforText.css */

body {
  font-family: 'Wix Madefor Text', sans-serif;
}

/* LoginPage.css */
.login-box {
  background-color: #fff;
  padding: 32px;
  box-shadow: 0px 4px 12px rgba(0, 0, 0, 0.1);
  border-radius: 8px;
  width: 100%; /* Ensure the container fits its content */
  display: flex;
  flex-direction: column;
  align-items: center; /* Centering content */
}

body {
  font-family: 'Wix Madefor Text', sans-serif;
}

.MuiTab-root {
  font-weight: bold;
  text-transform: none; /* Prevent uppercase transformation */
  color: #3a97ad;
  padding: 6px 12px;
}

.MuiTabs-indicator {
  background-color: #3a97ad; /* Theme color for the active tab indicator */
}

.MuiButton-root {
  font-weight: bold;
}

.MuiLink-root {
  color: #3a97ad; /* Theme color for links */
  font-weight: bold;
}

.MuiTab-root.Mui-selected {
  color: #3a97ad; /* Theme color for selected tab */
}

.MuiTab-root:hover {
  background-color: rgba(56, 111, 123, 0.1); /* Subtle background on hover */
}

.MuiTabs-flexContainer {
  justify-content: space-between; /* Ensure tabs are spaced correctly */
}

/* LoadingScreen.css */
.loading-screen {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #3a97ad;
  }
  
.App {
  text-align: center;
  padding: 10px;
}

textarea, select, button {
  width: 100%;
  margin: 5px 0;
}

textarea {
  height: 100px;
}

button {
  padding: 10px;
  background-color: #4CAF50;
  color: white;
  border: none;
  cursor: pointer;
}

button:hover {
  background-color: #45a049;
}

